import { __ } from '@wordpress/i18n';
import { InspectorControls, BlockControls, AlignmentToolbar } from '@wordpress/block-editor';
import { PanelBody, PanelRow, TabPanel, TextareaControl, ToggleControl, SelectControl, Dashicon } from '@wordpress/components';

// Settings Components
import Title from '../../Components/Title';
import BtnGroup from '../../Components/BtnGroup';
import Background from '../../Components/Background';
import Typography from '../../Components/Typography';
import BColor from '../../Components/BColor';
import SpaceControl from '../../Components/SpaceControl';
import BorderControl from '../../Components/BorderControl';

import options from './Const/options';
const { animateInOpt, effects, inEffects, outEffects, sequences, generalStyleTabs } = options;

const Settings = ({ attributes, setAttributes }) => {
	const { loop, animateIn, inEffect, inSequence, outEffect, outSequence, textAlign, background, content, typography, color, padding, border } = attributes;

	return <>
		<InspectorControls>
			<TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs}>{tab => <>
				{'general' === tab.name && <>
					<PanelBody className='bPlPanelBody help' title={__('Help', 'animated-text')}>
						<div className='helpItem'>
							<a href='https://bblockswp.com/docs/animated-text-block/' target='_blank' rel='noreferrer'><Dashicon icon='book' />{__('Read Documentation', 'animated-text')}</a>
						</div>

						<div className='helpItem rateUs'>
							<a href='https://wordpress.org/support/plugin/animated-text-block/reviews/#new-post' target='_blank' rel='noreferrer'>
								<span><Dashicon icon='star-filled' />{__('Would you please rate us?', 'animated-text')}</span>
								<span>{__('We are new and we need your help to grow!🙏', 'animated-text')}</span>
							</a>
						</div>
					</PanelBody>


					<PanelBody className='bPlPanelBody' title={__('Animated Text Settings', 'animated-text')}>
						<Title mt='0'>{__('Animate text:', 'animated-text')}</Title>
						<TextareaControl value={content} onChange={val => setAttributes({ content: val })} />

						<ToggleControl className='mt20' label={__('Loop', 'animated-text')} checked={loop} onChange={val => setAttributes({ loop: val })} />
						<small>{__('Loop will not work instantly in editor! you have to refresh the page.', 'animated-text')}</small>

						<PanelRow>
							<Title mt='0' mb='0'>{__('Animate in:', 'animated-text')}</Title>
							<BtnGroup value={animateIn} onChange={val => setAttributes({ animateIn: val })} options={animateInOpt} />
						</PanelRow>

						<PanelRow>
							<Title mt='0' mb='0'>{__('In Effect:', 'animated-text')}</Title>
							<SelectControl value={inEffect} onChange={val => setAttributes({ inEffect: val })} options={[...effects, ...inEffects]} />
						</PanelRow>

						<PanelRow>
							<Title mt='0' mb='0'>{__('In Sequence:', 'animated-text')}</Title>
							<SelectControl value={inSequence} onChange={val => setAttributes({ inSequence: val })} options={sequences} />
						</PanelRow>

						{loop && <>
							<PanelRow>
								<Title mt='0' mb='0'>{__('Out Effect:', 'animated-text')}</Title>
								<SelectControl value={outEffect} onChange={val => setAttributes({ outEffect: val })} options={[...effects, ...outEffects]} />
							</PanelRow>

							<PanelRow>
								<Title mt='0' mb='0'>{__('Out Sequence:', 'animated-text')}</Title>
								<SelectControl value={outSequence} onChange={val => setAttributes({ outSequence: val })} options={sequences} />
							</PanelRow>
						</>}
					</PanelBody>
				</>}


				{'style' === tab.name && <>
					<PanelBody className='bPlPanelBody' title={__('Animated Text', 'animated-text')}>
						<Background label={__('Background:', 'animated-text')} background={background} onChange={val => setAttributes({ background: val })} defaults={{ color: '#0000' }} />

						<Typography className='mt20' label={__('Typography:', 'animated-text')} typography={typography} onChange={val => setAttributes({ typography: val })} defaults={{ fontSize: 22 }} />

						<BColor label={__('Color:', 'animated-text')} value={color} onChange={val => setAttributes({ color: val })} defaultColor='#333' />

						<SpaceControl className='mt20' label={__('Padding:', 'animated-text')} space={padding} onChange={val => setAttributes({ padding: val })} defaults={{ vertical: '15px', horizontal: '15px' }} />

						<BorderControl label={__('Border:', 'animated-text')} border={border} onChange={val => setAttributes({ border: val })} defaults={{ radius: '5px' }} />
					</PanelBody>
				</>}
			</>}</TabPanel>
		</InspectorControls>


		<BlockControls>
			<AlignmentToolbar value={textAlign} onChange={val => setAttributes({ textAlign: val })} />
		</BlockControls>
	</>;
};
export default Settings;